@import url(https://fonts.googleapis.com/css?family=Ubuntu|Lato);
body{
    font-family: 'Ubuntu', 'Lato', Helvetica, sans-serif;
}

.navbar{
    height: 4rem;
    background: #191919;
}

.navbar-brand{
    font-size: 1.5rem;
    color:white;
}

.nav-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.arrow{
    width:0;
    height:0;
    position: absolute;
    display:none;
    top: 0;
    border-right:12px transparent solid;
    border-left:12px transparent solid;
    border-top: 15px #f4a460 solid;
    border-bottom: 0 transparent solid;
}

.nav-link, .nav-link:hover{
    color: white;
}

.active{
    color: #f4a460!important;
}
.container{
    padding-top: 2rem;
    
}

.clock-container{
    margin: 2rem 0;
}

.clock{
    width: 15rem;
    height: 15rem;
    border: 3px darkslategray solid;
    border-radius: 50%;
    display:flex;
    justify-content: center;
    align-items: center;
    background: #4a7edd;
}

#clock-text{
    font-size: 3rem;
    color: #000;
}

#controls{
    display:flex;
    justify-content: space-between;
    width: 12rem;
    margin: 0 auto;
}

button{
    width: 5rem;
}
.form-group{
    width: 15rem;
}

input:focus::-webkit-input-placeholder{
    color: transparent;
}

input:focus::-moz-placeholder{
    color:transparent;
}

input:focus::-ms-input-placeholder{
    color:transparent;
}

.btn{
    margin-top: 1rem;
}
